import React from "react"
import PropTypes from "prop-types"
import MyTableHeaderExt from "./_ext"

class MTHstyled extends MyTableHeaderExt {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    const { header } = this.props
    let _style = this.getStyle(this.props)
    return (
      <div className="mth-styled" style={_style}>
        {header}
      </div>
    )
  }
}

export default MTHstyled
